import { Component, Element, h, Host, Method, Prop } from '@stencil/core';

let id = 0;

@Component({
	tag: 'o-tab',
	styleUrl: 'o-tab.scss',
	shadow: true
})
export class OTab {
	private componentId = `tab-${++id}`;
	private tab?: HTMLElement;

	@Element() host!: HTMLOTabElement;

	/** The name of the tab panel the tab will control. The panel must be located in the same tab group. */
	@Prop({ reflect: true }) panel = '';

	/** Set to true to draw the tab in an active state. */
	@Prop({ reflect: true }) active = false;

	/** Set to true to draw the tab in a disabled state. */
	@Prop({ reflect: true }) disabled = false;

	/** Sets focus to the tab. */
	@Method()
	async setFocus(options?: FocusOptions) {
		this.tab?.focus(options);
	}

	/** Removes focus from the tab. */
	@Method()
	async removeFocus() {
		this.tab?.blur();
	}

	render() {
		const tabGroup: HTMLOTabGroupElement | null = this.host.closest('o-tab-group');
		const placement = tabGroup?.placement ?? 'top';

		return (
			<Host>
				<div id={this.host.id || this.componentId} class="relative">
					<div
						ref={(el) => (this.tab = el)}
						class={`flex items-center whitespace-nowrap select-none w-full font-bold focus:outline-none focus-visible:outline-[3px] focus:outline-offset-0 focus-visible:outline-accent_blue_1 pt-3.5 pb-4 mt-[3px] mx-[3px] justify-center text-sm
              			${this.disabled ? 'text-tab_grey cursor-not-allowed' : 'cursor-pointer'}
              			${this.active ? 'text-accent_blue_1' : this.disabled ? '' : 'text-table_header hover:text-accent_blue_1'}
              			${this.active && !this.disabled ? 'text-accent_blue_1' : ''}`}
						role="tab"
						aria-disabled={this.disabled ? 'true' : 'false'}
						aria-selected={this.active ? 'true' : 'false'}
						tabindex={this.disabled || !this.active ? '-1' : '0'}
					>
						<slot />
					</div>
					<div
						class={`absolute left-[3px] ${this.active ? 'bg-accent_blue_1' : ''}
              ${placement === 'top' ? 'bottom-0 h-1 w-full' : ''}
            `}
					></div>
				</div>
			</Host>
		);
	}
}
